.half-card {
  width: calc(50% - 16px);
  height: 380px;
  margin: 8px;
  transform: scale(1);
  transition: transform 0.5s;
  position: relative;
}
.half-card .hashteg {
  height: 16px;
  overflow: hidden;
}
.half-card > .half-line-news {
  margin-bottom: 15px;
}
.half-card--inner {
  height: 280px;
  overflow: hidden;
}
.half-card--inner > a > picture > img {
  height: 280px;
}
.half-card--inner.half-card-news .half-card-news__title {
  margin-top: 75px;
}
.half-card--inner.half-card-news .tag {
  top: 54px;
}
.half-card--inner.half-card-news__hashteg {
  bottom: 40px;
}

@media (max-width: 1024px) {
  .half-card {
    width: calc(66.66666% - 16px);
    height: 355px;
  }
  .half-card--adaptive {
    width: 100%;
  }
  .half-card--inner {
    width: calc(50% - 16px);
    height: 250px;
  }
  .half-card--inner > a > picture > img {
    height: 250px;
  }
  .half-card--inner.half-card-news .half-card-news__title {
    margin-top: 42px;
  }
  .half-card--inner.half-card-news .tag {
    top: 24px;
  }
  .half-card--inner.half-card-news .stamp {
    bottom: -85px;
  }
  .half-card--inner.half-card-news .hashteg {
    bottom: 53px;
  }
}
@media (max-width: 767px) {
  .half-card {
    width: 100%;
    height: 310px;
  }
  .half-card--inner {
    height: auto;
  }
  .half-card--inner > a > picture > img {
    height: auto;
  }
  .half-card--inner.half-card-news .half-card-news__title {
    margin-bottom: 40px;
  }
  .half-card--inner.half-card-news .stamp {
    bottom: -30px;
  }
  .half-card--inner.half-card-news .hashteg {
    bottom: 50px;
  }
}